}

html, body, #root {
  height: 100%;
}

#header {
  mix-blend-mode: difference;
  height: 9.6rem;
  position: absolute !important;
  z-index: 9;
}

#header #nav {
  display: flex;
  align-items: flex-start;
  transition: height 0.3s ease-in-out;
}

#header #nav .logo {
  text-decoration: none;
  color: #0a14c8;
  font-size: 1.6rem;
  line-height: 1;
  transition: all 0.5s ease-in-out !important;
  z-index: 99;
}

#header #nav .logo span {
  display: block;
}

#header #nav .menu {
  color: #0a14c8;
  font-size: 2.4rem;
  margin-left: auto;
  transition: opacity 0.3s ease-in-out;
}

#header #nav-web {
  opacity: 1 !important;
  position: absolute;
  right: 0;
  display: none;
  justify-content: flex-end;
  transition: opacity 0.3s ease-in-out;
}

#header #nav-web a {
  color: #0a14c8 !important;
  text-decoration: none;
  position: relative;
  display: inline;
  transition: all 0.3s 2s ease-in-out;
}

#header #nav-web a .line {
  width: 0px;
  height: 4px;
  position: absolute;
  left: 0px;
  bottom: 4px;
}

#header #nav-web a .line::after {
  content: '';
  background-color: #0a14c8;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

#header #nav-web a.active .line {
  width: 16px;
  transition: width .3s;
}

#header #nav-web a:nth-child(2) {
  margin-right: 0 !important;
}

#header #nav-web a:last-child {
  font-size: 1.2rem;
  line-height: 2;
  position: absolute;
  right: 0rem !important;
}

.project .contentWrap.intro .pageTitle {
  line-height: 1.4 !important;
